import React from "react";
import { Modal, Button, Input, DatePicker } from 'antd';

import { Select } from 'antd';

import moment from 'moment';

const { Option } = Select;



export default class App extends React.Component {
  constructor(props){
    super(props)
    this.state = {
      eventTitle: null,
      eventDate: null, 
      data: null
  };
  }


  componentDidMount(){
    console.log("Event title=========", this.props)
    this.setState({
      eventTitle: this.props.data ? this.props.data.title : null ,
      eventDate: this.props.args ? this.props.args.dateStr : null, 
      eventDateEnd: null, 
      data: this.props.data ? this.props.data : null
    })
  }
  

  handleChangeTitle(event){
    console.log('target event', event.target.value)
    this.setState({
      eventTitle: event.target.value
    })
  }

  onChange(date, dateString) {
    console.log("Data log", date, dateString);
    this.setState({
      eventDate: dateString
    })
  }

  onChangeDateEnd(date, dateString) {
    console.log("Data log", date, dateString);
    this.setState({
      eventDateEnd: dateString
    })
  }

  cleanState(){
    this.props.handleCancel()
    this.setState({
      eventTitle: null,
      eventDate: null,
      loading: false,
    })
    console.log("State cleaned", this.state)
  }

  onSubmit(){
    this.setState({
      loading: true
    })
    setTimeout(() => {
      this.props.handleOk({date: this.state.eventDate, endDate: this.state.eventDateEnd, title: this.state.eventTitle})
      this.setState({
        loading: false,
        eventTitle: null,
        eventDate: null,
        eventDateEnd: null
      })
      console.log("State cleaned", this.state)
    }, 2000)
    
  }
  

  render() {
    console.log("props modal", this.props)
    return (
      <div>
        <Modal
          visible={this.props.visible}
          title="Ajouter un évènement"
          onOk={this.props.handleOk}
          onCancel={ this.cleanState.bind(this) }
          footer={[
            <Button key="back" 
              onClick={this.cleanState.bind(this)}
            >
              Return
            </Button>,
            <Button 
              key="submit" 
              type="primary" 
              loading={this.state.loading} 
              onClick={ this.onSubmit.bind(this) }
            >
              Submit
            </Button>,
          ]}
        >
          <form>
            <div>
              <Input 
                label="Titre de la tâche"
                placeholder="Titre"
                type="text"
                value={this.state.eventTitle}
                onChange={this.handleChangeTitle.bind(this)} 
              />
            </div>
            <div>
            <br />
            <Select defaultValue="technicien 1" style={{ width: "100%" }} loading>
              <Option value="technicien 1">Technicien 1</Option>
              <Option value="technicien 2">Technicien 2</Option>
              <Option value="technicien 3">Technicien 3</Option>
              <Option value="technicien 4">Technicien 4</Option>
              <Option value="technicien 5">Technicien 5</Option>
              <Option value="technicien 6">Technicien 6</Option>
              <Option value="technicien 7">Technicien 7</Option>
              <Option value="technicien 8">Technicien 8</Option>
              <Option value="technicien 9">Technicien 9</Option>
            </Select>
            </div>
            <div>
              <br />

              <label>Date de début</label> 
              <DatePicker 
                onChange={this.onChange.bind(this)} 
                // defaultValue={moment('2020-07-01', 'YYYY-MM-DD')}
                defaultValue={moment(this.props.args ? (this.props.args.dateStr , 'YYYY-MM-DD') : moment('2020-07-01', 'YYYY-MM-DD') )}
                // value={moment(this.state.eventDate, 'YYYY-MM-DD')}
                size="default"
                style={{
                  width: "100%",
                }}
              />

              <br />
              <br />
              <label>Date de fin</label> 
              <DatePicker 
                onChange={this.onChangeDateEnd.bind(this)} 
                // defaultValue={moment('2020-07-01', 'YYYY-MM-DD')}
                defaultValue={moment(this.props.args ? (this.props.args.dateStr , 'YYYY-MM-DD') : moment('2020-07-01', 'YYYY-MM-DD') )}
                // value={moment(this.state.eventDate, 'YYYY-MM-DD')}
                size="default"
                style={{
                  width: "100%",
                }}
              />
            </div>
          </form>

        </Modal>
      </div>
    );
  }
}
