import React from "react";
import FullCalendar from "@fullcalendar/react";
import dayGridPlugin from "@fullcalendar/daygrid";
import timeGridPlugin from "@fullcalendar/timegrid";
import interactionPlugin from "@fullcalendar/interaction"; // needed for dayClick

import MyModal from '../Snipped/Mdal/index'
import "./styles.css";

// must manually import the stylesheets for each plugin
// import "@fullcalendar/core/main.css";
import "@fullcalendar/daygrid/main.css";
import "@fullcalendar/timegrid/main.css";

export default class Calendar extends React.Component {
  calendarComponentRef = React.createRef();

  state = {
    calendarWeekends: true,
    calendarEvents: [
      // initial event data
      { 
        id: 1,
        title: "Maintenance éolienne", 
        start: new Date(), 
      }
    ],
    loading: false,
    visible: false,
    editEvent: null, 
    arg: null
  };

  render() {
    return (
      <div className="demo-app">
        {/* <div className="demo-app-top">
          <button onClick={this.toggleWeekends}>toggle weekends</button>&nbsp;
          <button onClick={this.gotoPast}>go to a date in the past</button>
          &nbsp; (also, click a date/time to add an event)
        </div> */}
        <div className="demo-app-calendar">
          <FullCalendar
            defaultView="dayGridMonth"
            header={{
              left: "prev,next today",
              center: "title",
              right: "dayGridMonth,timeGridWeek,timeGridDay,listWeek"
            }}
            plugins={[dayGridPlugin, timeGridPlugin, interactionPlugin]}
            ref={this.calendarComponentRef}
            weekends={this.state.calendarWeekends}
            events={this.state.calendarEvents}
            dateClick={this.handleDateClick}
            eventClick={this.handleEventClick.bind(this)}
          />
        </div>

        <MyModal 
            visible={this.state.visible} 
            loading={this.state.loading} 
            handleOk={this.handleOk.bind(this)} 
            handleCancel={this.handleCancel.bind(this)} 
            data={this.state.editEvent}
            arg={this.state.arg}
        />
      </div>
    );
  }

  showModal = (arg) => {
    this.setState({
      visible: true,
      arg: arg
    });
  };

  handleOk = (values) => {
    console.log("Values", values)
    this.setState({
      loading: true,
      // add new event data
    });

    this.setState({ 
      visible: false,
      loading: values.loading,
      calendarEvents: this.state.calendarEvents.concat({
        // creates a new array
        id: 2,
        title: values.title,
        start: values.date,
        end: values.endDate,
        allDay: true
      })
    });
  };

  handleCancel = () => {
    this.setState({ visible: false });
  };

  toggleWeekends = () => {
    this.setState({
      // update a property
      calendarWeekends: !this.state.calendarWeekends
    });
  };

  gotoPast = () => {
    let calendarApi = this.calendarComponentRef.current.getApi();
    calendarApi.gotoDate("2000-01-01"); // call a method on the Calendar object
  };

  handleDateClick = arg => {
    console.log("Add new event function", arg)
    this.showModal(arg)
    // if (window.confirm("Would you like to add an event to " + arg.dateStr + " ?")) {
    //   this.setState({
    //     // add new event data
    //     calendarEvents: this.state.calendarEvents.concat({
    //       // creates a new array
    //       title: "New Event",
    //       start: arg.date,
    //       allDay: arg.allDay
    //     })
    //   });
    // }
  };

  handleEventClick(info){
    console.log("Infos event click", info.event.id, info.event.title, info.event.start)
    this.setState({
      visible: true,
      editEvent: {
        title: info.event.title
      }
    });
  }
}
